import { parseModule } from "meriyah";
import { generate } from "astring";
import { makeTraveler } from "astravel";
import { encodeUrl } from "./url";

// i am a cat. i like to be petted. i like to be fed. i like to be

// js rewiter is NOT finished

// location
// window
// self
// globalThis
// this
// top
// parent

const Ignore = Function.prototype;


export function rewriteJs(js: string, origin?: URL) {
    try {
        const ast = parseModule(js, {
            module: true,
            webcompat: true
        });
    
        const identifierList = [
            "window",
            "self",
            "globalThis",
            "parent",
            "top",
            "location",
            "opener"
        ]
    
        const customTraveler = makeTraveler({
            go: (node) => {
                if (this[node.type]) {
                    this[node.type](node);
                } else {
                    console.log(node)
                }
            },

            Program: (node) => {
                if (node.body) {
                    for (let i = 0; i < node.body.length; i++) {
                        this.go(node.body[i]);
                    }
                }
            },
            
            ImportDeclaration: (node) => {
                node.source.value = encodeUrl(node.source.value, origin);
            },
    
            ImportExpression: (node) => {
                node.source.value = encodeUrl(node.source.value, origin);
            },
    
            ExportAllDeclaration: (node) => {
                node.source.value = encodeUrl(node.source.value, origin);
            },
    
            ExportNamedDeclaration: (node) => {
                if (node.source) node.source.value = encodeUrl(node.source.value, origin);
            },

            ExpressionStatement: (node) => {
                this.go(node.expression);
            },

            FunctionDeclaration: (node) => {
                if (node.params) {
                    node.params = node.params.map((param) => {
                        if (param.type !== "Identifier") this.go(param);

                        return param;
                    });
                }

                this.go(node.body);
            },

            BlockStatement: (node) => {
                if (node.body) {
                    for (let i = 0; i < node.body.length; i++) {
                        this.go(node.body[i]);
                    }
                }
            },

            ArrayExpression: (node) => {
                for (let i = 0; i < node.elements.length; i++) {
                    this.go(node.elements[i]);
                }
            },

            CallExpression: (node) => {
                if (node.arguments) {
                    for (let i = 0; i < node.arguments.length; i++) {
                        this.go(node.arguments[i]);
                    }
                }

                this.go(node.callee);
            },

            AssignmentExpression: (node) => {
                // visiting node.left gets tricky when dealing with multiple scopes, will be implemented later
                this.go(node.left);
                this.go(node.right);
            },

            VariableDeclaration: (node) => {
                for (let i = 0; i < node.declarations.length; i++) {
                    this.go(node.declarations[i]);
                }  
            },

            VariableDeclarator: (node) => {
                this.go(node.init);
            },

            MemberExpression: (node) => {
                this.go(node.object);
            },

            IfStatement: (node) => {
                this.go(node.test);
                this.go(node.consequent);
                this.go(node.alternate);
            },

            LogicalExpression: (node) => {
                this.go(node.left);
                this.go(node.right);
            },
            
            Identifier: (node) => {
                if (identifierList.includes(node.name)) {
                    node.name = "__" + node.name; 
                }
            }
        });

        customTraveler.go(ast);
    
        return generate(ast);
    } catch (error) {
        console.log(js);
        // throw new Error(error);

        return js;
    }
}
